import React, { useState, useEffect } from "react";
import { Chart, registerables } from "chart.js";
import { Line } from "react-chartjs-2";
import axios from "axios";
import { Container, Grid, Card, Typography, CircularProgress } from "@mui/material";
import LiveDataCards from "../components/LiveDataCards";
import LiveDataTable from "../components/LiveDataTable";
import LiveMap from "../components/LiveMap";
import StatusAlert from "../components/StatusAlert";

// ✅ Register Chart.js components
Chart.register(...registerables);

const API_URL = "http://localhost:8000/data";
const WS_URL = "ws://localhost:8000";

const MAX_POINTS = 20; // ✅ Limit chart to last 20 points to avoid overcrowding

const Dashboard = () => {
  const [data, setData] = useState([]);
  const [pm25Chart, setPm25Chart] = useState(null);
  const [tempChart, setTempChart] = useState(null);
  const [humidityChart, setHumidityChart] = useState(null);
  const [windChart, setWindChart] = useState(null);

  useEffect(() => {
    axios.get(API_URL).then((response) => {
      const initialData = response.data.slice(-MAX_POINTS); // ✅ Load last 20 points
      setData(initialData);
      updateCharts(initialData);
    });

    const socket = new WebSocket(WS_URL);
    socket.onopen = () => console.log("✅ WebSocket Connected");

    socket.onmessage = (event) => {
      const newData = JSON.parse(event.data); // ✅ Expecting one data point at a time
      setData((prevData) => {
        const updatedData = [...prevData, newData].slice(-MAX_POINTS); // ✅ Append & keep last 20 points
        updateCharts(updatedData);
        return updatedData;
      });
    };

    socket.onerror = (error) => console.error("🚨 WebSocket Error:", error);
    socket.onclose = () => console.warn("⚠️ WebSocket Disconnected");

    return () => socket.close();
  }, []);

  const updateCharts = (data) => {
    const timestamps = data.map((d) => d.time);
    setPm25Chart(createChartData(timestamps, data.map((d) => d.pm25), "PM2.5 Levels", "red"));
    setTempChart(createChartData(timestamps, data.map((d) => d.temp), "Temperature (°C)", "blue"));
    setHumidityChart(createChartData(timestamps, data.map((d) => d.humidity), "Humidity (%)", "green"));
    setWindChart(createChartData(timestamps, data.map((d) => d.wind), "Wind Speed (km/h)", "purple"));
  };

  const createChartData = (labels, data, label, color) => ({
    labels,
    datasets: [{ label, data, borderColor: color, fill: false }],
  });

  return (

    <Container maxWidth="lg" sx={{ py: 4, pl: { xs: 0, md: 10 } }}>
      <Typography variant="h4" color="primary" gutterBottom textAlign="center">
        Real-Time Air Quality Dashboard
      </Typography>

      <Typography variant="h4" color="primary" gutterBottom>
        Real-Time Air Quality Dashboard
      </Typography>

      <StatusAlert pm25={data.pm25} />  {/* ✅ Show alert if air quality is bad */}
      <LiveDataCards data={data} />      {/* ✅ Show real-time values in cards */}
      <LiveMap lat={data.lat} lon={data.lon} pm25={data.pm25} />  {/* ✅ Map */}
      <LiveDataTable data={data} />      {/* ✅ Show historical records in a table */}

      <Grid container spacing={4}>
        <Grid item xs={12} md={6}>
          <Card sx={{ p: 4, boxShadow: 3 }}>
            <Typography variant="h6" color="secondary">PM2.5 Levels</Typography>
            {pm25Chart ? <Line data={pm25Chart} /> : <CircularProgress />}
          </Card>
        </Grid>

        <Grid item xs={12} md={6}>
          <Card sx={{ p: 4, boxShadow: 3 }}>
            <Typography variant="h6" color="secondary">Temperature (°C)</Typography>
            {tempChart ? <Line data={tempChart} /> : <CircularProgress />}
          </Card>
        </Grid>

        <Grid item xs={12} md={6}>
          <Card sx={{ p: 4, boxShadow: 3 }}>
            <Typography variant="h6" color="secondary">Humidity (%)</Typography>
            {humidityChart ? <Line data={humidityChart} /> : <CircularProgress />}
          </Card>
        </Grid>

        <Grid item xs={12} md={6}>
          <Card sx={{ p: 4, boxShadow: 3 }}>
            <Typography variant="h6" color="secondary">Wind Speed (km/h)</Typography>
            {windChart ? <Line data={windChart} /> : <CircularProgress />}
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};


const LiveDataCards = ({ data }) => {
  return (
    <Grid container spacing={2}>
      <Grid item xs={12} md={3}>
        <Card sx={{ textAlign: "center", p: 2 }}>
          <Typography variant="h6" color="textSecondary">PM2.5</Typography>
          <Typography variant="h4" color="primary">{data.pm25} µg/m³</Typography>
        </Card>
      </Grid>
      <Grid item xs={12} md={3}>
        <Card sx={{ textAlign: "center", p: 2 }}>
          <Typography variant="h6" color="textSecondary">Temperature</Typography>
          <Typography variant="h4" color="secondary">{data.temp} °C</Typography>
        </Card>
      </Grid>
      <Grid item xs={12} md={3}>
        <Card sx={{ textAlign: "center", p: 2 }}>
          <Typography variant="h6" color="textSecondary">Humidity</Typography>
          <Typography variant="h4" color="green">{data.humidity} %</Typography>
        </Card>
      </Grid>
      <Grid item xs={12} md={3}>
        <Card sx={{ textAlign: "center", p: 2 }}>
          <Typography variant="h6" color="textSecondary">Wind Speed</Typography>
          <Typography variant="h4" color="blue">{data.wind} km/h</Typography>
        </Card>
      </Grid>
    </Grid>
  );
};

import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from "@mui/material";

const LiveDataTable = ({ data }) => {
  return (
    <TableContainer component={Paper} sx={{ mt: 3 }}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell><b>Time</b></TableCell>
            <TableCell><b>PM2.5</b></TableCell>
            <TableCell><b>Temperature (°C)</b></TableCell>
            <TableCell><b>Humidity (%)</b></TableCell>
            <TableCell><b>Wind Speed (km/h)</b></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row, index) => (
            <TableRow key={index}>
              <TableCell>{row.time}</TableCell>
              <TableCell>{row.pm25} µg/m³</TableCell>
              <TableCell>{row.temp} °C</TableCell>
              <TableCell>{row.humidity} %</TableCell>
              <TableCell>{row.wind} km/h</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";

const LiveMap = ({ lat, lon, pm25 }) => {
  return (
    <MapContainer center={[lat, lon]} zoom={10} style={{ height: "300px", width: "100%" }}>
      <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
      <Marker position={[lat, lon]}>
        <Popup>PM2.5: {pm25} µg/m³</Popup>
      </Marker>
    </MapContainer>
  );
};

import { Alert } from "@mui/material";

const StatusAlert = ({ pm25 }) => {
  if (pm25 > 100) {
    return <Alert severity="error">⚠️ High Pollution Level! PM2.5: {pm25} µg/m³</Alert>;
  } else if (pm25 > 50) {
    return <Alert severity="warning">⚠️ Moderate Pollution! PM2.5: {pm25} µg/m³</Alert>;
  }
  return <Alert severity="success">✅ Air Quality is Good!</Alert>;
};


export default Dashboard;
